img {
  border-radius: 4px;
  margin: 5px;
  width: 150px;
}
.doc-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px;
  background-color: whitesmoke;
}
.doctors {
  margin: 30px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.icon-info {
  display: block;
}
h4 {
  margin: 5px;
}
li {
  margin: 5px;
}
span {
  margin: 4px;
  font-weight: bold;
  color: rgb(116, 121, 121);
  text-decoration: none;
}
.outsource {
  background-color: rgb(57, 238, 238);
  display: flex;
  border-radius: 12px;
  padding: 6px;
  flex-wrap: wrap;
}
.outsource a {
  margin: 5px;
  text-decoration: underline;
}
.iconss {
  margin: 0px 10px;
}
