.abt-container {
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
    border-radius: 12px;
    padding: 12px;
    background-color: aliceblue;
    border: 12px;
}
.head-article {
    margin-top: 20px;
    text-align: center;
    padding: 5px;
    margin-right: 7px;
    color:  rgb(9, 6, 6);
}

.para-article {
    font-size: 1.5rem;
    margin-left: 3%;
    background-color: darkgray;
    border-radius: 12px;
    margin-top: 20px;
    text-align: center;
    padding: 10px;
    margin-right: 5%;
    color:  rgb(19, 0, 0);
}
span {
    text-decoration: underline;
    color: black;
}

.hos-name {
    text-decoration: none;
    color:rgb(185, 14, 68);
}
.why-us {
    margin-top: 30px;
    margin-left: 8px;
    padding: 7px;
    color: rgb(14, 11, 11);
}
.whyus-span {
    text-decoration: none;
    font-size: 1.5rem;
}
.why-us li {
    margin: 10px;
    font-size: 1.3rem;
}
.why-us ul {
    list-style: circle;
}
