.header {
  background-color: white;
  display: flex;
  justify-content: space-between;
}
.logo {
  margin-top: 1em;
  margin-left: 1em;
  width: 150px;
}
nav {
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  width: 40%;
  background-color: rgb(49, 154, 196);
  padding-top: 50px;
  z-index: 2;
  height: 100vh;
  padding-left: 20px;
  padding-right: 15px;
  flex-direction: column;
}
nav li a {
  color: white;
  text-decoration: none;
  font-size: 1.6rem;
}
nav li {
  margin-top: 45px;
  list-style: none;
}
button {
  background-color: rgb(91, 172, 174);
  border-radius: 20px;
  font-weight: bolder;
  border: none;
  font-size: 1.3rem;
  width: 40px;
}
.hamIcon {
  margin-top: 0.5em;
  margin-right: 1em;
  width: 40px;
}
.nav-hide {
  transform: translateX(100%);
  transition: transform 0.3s ease-in;
}
.nav-show {
  transform: translateX(0);
  transition: transform 0.3s ease-in;
}