.head {
  height: 200px;
  width: 300px;
}
.head-text {
  text-align: center;
  background-color: rgba(182, 204, 218, 0.8);
  font-size: 1.1rem;
  border-radius: 12px;
  padding: 12px;
  color: rgb(6, 6, 6);
}

.head-text span {
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: bolder;
  color: black;
}
.head-text p {
  font-size: 1.2rem;
  margin-top: 12px;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.quote-para {
  font-style: italic;
  font-weight: bolder;
}
.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 60px;
}
.img-container {
  margin-top: 12px;
  width: 100%;
  height: 70vw;
  background: url("../Images/img1.JPEG");
  animation: slide 10s infinite;
  background-repeat: no-repeat;
  background-size: cover;
}
@keyframes slide {
  25% {
    width: 100%;
    height: 70vw;
    background-repeat: no-repeat;
    background: url("../Images/img2.JPEG");
    background-size: cover;
  }
}
.box-contents {
  margin-top: 1em;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}
.box {
  margin: 1em;
  border: 1px solid whitesmoke;
  box-shadow: -4px 2px 5px rgb(213, 212, 212);
  border-radius: 12px;
  height: 150px;
  width: 40vw;
  padding: 12px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
}
.box h5 {
  margin-left: 9px;
}
.icons {
  margin-bottom: .5em;
  height: 2.5em;
  color: rgb(196, 143, 247);
}
.box-contents h5 {
  font-size: 1.2rem;
}
.chat {
  margin-top: 12px;
  border-radius: 12px;
  width: 65vw;
}
a {
  text-decoration: none;
  color: rgb(32, 38, 39);
}
.quote {
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
  max-width: 80vw;
  border-radius: 20px;
  padding: 12px;
  background-color: aliceblue;
}